// WD Purple Theme Background colors
  @mixin wd-purple-bgColor-themeDark {
    background-color: $wd-purple-themeDark;
  }

  @mixin wd-purple-bgColor-themeDarkAlt {
    background-color: $wd-purple-themeDarkAlt;
  }

  @mixin wd-purple-bgColor-themeDarker {
    background-color: $wd-purple-themeDarker;
  }

  @mixin wd-purple-bgColor-themePrimary {
    background-color: $wd-purple-themePrimary;
  }

  @mixin wd-purple-bgColor-themeSecondary {
    background-color: $wd-purple-themeSecondary;
  }

  @mixin wd-purple-bgColor-themeTertiary {
    background-color: $wd-purple-themeTertiary;
  }

  @mixin wd-purple-bgColor-themeLight {
    background-color: $wd-purple-themeLight;
  }

  @mixin wd-purple-bgColor-themeLighter {
    background-color: $wd-purple-themeLighter;
  }

  @mixin wd-purple-bgColor-themeLighterAlt {
    background-color: $wd-purple-themeLighterAlt;
  }

