// WD Brand Background colors
  @mixin wd-bgColor-wdPurple {
    background-color: $wd-color-wdPurple;
  }
  @mixin wd-bgColor-wdGreen {
    background-color: $wd-color-wdGreen;
  }
  @mixin wd-bgColor-wdOrange {
    background-color: $wd-color-wdOrange;
  }

// WD Brand Border colors
  @mixin wd-borderColor-wdPurple {
    border-color: $wd-color-wdPurple;
  }
  @mixin wd-borderColor-wdGreen {
    border-color: $wd-color-wdGreen;
  }
  @mixin wd-borderColor-wdOrange {
    border-color: $wd-color-wdOrange;
  }
